angular.module('app.cars').config(carConfig); 

carConfig.$inject = ['$stateProvider'];
function carConfig($stateProvider) {
    $stateProvider.state('base.cars', {
        url: '/cars',
        views: {
            '@': {
                templateUrl: 'cars/controllers/index.html',
                controller: CarsController,
                controllerAs: 'vm',
            },
        },
        ncyBreadcrumb: {
            label: 'Cars',
        },
    });
}

